import { Component, ComponentFactoryResolver, ElementRef, NgZone, OnInit , VERSION, ViewChild} from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AlertController, NavController } from '@ionic/angular';
import { ParkingServiceService } from './parking-service.service';
import { Parking, ParkingReservation } from 'src/model/parking';
import { Observable } from 'rxjs';
import { NavigationExtras, Router } from '@angular/router';

import { GeneralFunctions } from '../../general-functions';

@Component({
  selector: 'app-gestion-parking',
  templateUrl: './gestion-parking.page.html',
  styleUrls: ['./gestion-parking.page.scss'],
})
export class GestionParkingPage implements OnInit {

  inputStart:boolean;
  inputEnd:boolean;
  parkings: any[] = [];
  isBottomSheetOpened = false;
  localstorageReservations: ParkingReservation[] = [];
  registerForm:any;
  bookingForm:FormGroup;
  reservation;
  currentSalle:string;
  parkingName:string;
  parkingHistorical = [];
  multiplicateReservation:boolean;
  wrongReservation:boolean;
  convertedDate ;
  localISOTime;
  currentDate;
  showReservationsContent:boolean =true;
  notification:Observable<any[]>

  constructor(
    private parkingService : ParkingServiceService,
    private fb : FormBuilder,
    public alertController: AlertController ,
    public router : Router,
    public generalFunctions : GeneralFunctions,
    private navigateController: NavController
  ) {}

  initializeForm():void{
    this.bookingForm = this.fb.group({
      dateStart:'',
      dateEnd:'',
      references: this.fb.array([this.fb.control('')])
    });
  }

  ngAfterViewInit(){
    this.parkings = this.parkingService.getParkings();
    this.generalFunctions.gestionDeReservation(this.parkings,'ParkingReservations','ParkingHistoricalReservations');
  }

  ngOnInit() {
    let parkings = [{"id":"ASC A RDC","color":"#31bd8c","type":"libre","floor":"B1-UL0"},
      {"id":"ASC C RDC","color":"#1194ff","type":"reserve","floor":"B1-UL0"},
      {"id":"ASC E RDC","color":"#dd1185","type":"occupe","floor":"B1-UL0"}]
    localStorage.setItem('parkings', JSON.stringify(parkings));
    this.initializeForm();
    setInterval(() => {
      this.generalFunctions.refreshComponent(this.ngAfterViewInit());
    }, 1000);
  }

  goToMap(index: number) {
    console.log(index);
    localStorage.setItem('selectedparking', String(index));
    this.navigateController.navigateForward(['/tabs/map'])
  }

}
